.footer__road{
  min-height: 300px;
//   margin-left: 300px;
  background-size: 30% 100px;
  /*background-image: linear-gradient(to right, #7cb9fd 2px, transparent 1px), linear-gradient(to bottom, #319baa 2px, transparent 1px);*/
  /*background: repeating-linear-gradient(-90deg, transparent, transparent 6px, #464646 6px, #464646 50%), repeating-linear-gradient(0deg, #464646, #464646 20px, #ffffff 2px, #ffffff 50%);*/
  background: repeating-linear-gradient(-90deg, transparent, transparent 6px, $color-asphalt-gray 6px, $color-asphalt-gray 50%), repeating-linear-gradient(0deg, $color-asphalt-gray, $color-asphalt-gray 20px, #ffffff 2px, #ffffff 50%);
  width: 1440px;
  height: 100%;
  transform: perspective(100px) rotateX(55deg) scale(0.6);
  z-index: -1;
  -webkit-animation: myanim 0.7s infinite;
  -moz-animation: myanim 0.7s infinite;
  -o-animation: myanim 0.7s infinite;
  animation: myanim 0.7s infinite;

  &.footer__road--night {
    background-size: 20px 20px;
    background-image: linear-gradient(to right, #7cb9fd 2px, transparent 1px), linear-gradient(to bottom, #319baa 2px, transparent 1px);
    -webkit-animation: myanim2 0.7s infinite;
    -moz-animation: myanim2 0.7s infinite;
    -o-animation: myanim2 0.7s infinite;
    animation: myanim2 0.7s infinite;
  }
}

@-webkit-keyframes myanim {
  0% {
      background-position: 0 0px;
      background-size: 30% 100px;
  }
  25% {
      background-position: 0 25px;
      background-size: 30% 100px;
  }
  50% {
      background-position: 0 50px;
      background-size: 30% 100px;
  }
  75% {
      background-position: 0 75px;
      background-size: 30% 100px;
  }
  100% {
      background-position: 0 100px;
      background-size: 30% 100px;
  }
}

