
.footer{
    // height: 700px;

    position: relative;
    padding: 0 10px;

    @include bp($xs){
      padding: 0;
      height: 560px;
    }
}

.footer__logoLink{
    height: 60px;

    &:hover .footer__logo{
        -webkit-animation: rotate-vert-center 0.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
        animation: rotate-vert-center 0.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
      }
      @include bp($xs){
        height: 113px;
    }
}
.footer__logo{
  width: 60px;
  max-width: 60px;
  @include bp($xs){
    width: 113px;
    max-width: 113px;
  }
}
  
  
  @-webkit-keyframes rotate-vert-center {
    0% {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
    }
    100% {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
  }
  @keyframes rotate-vert-center {
    0% {
      -webkit-transform: rotateY(0);
              transform: rotateY(0);
    }
    100% {
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
  }


.footer__outerItem{
    width: 54px;
    height: 54px;
    border-bottom: 2px solid $color-black;
    border-left: 2px solid $color-black;
    margin-left: 30px;
    position: relative;
    @include bp($xs){
      margin-left: 56px;
    }
}

.footer__leftWrapper{
    display: flex;
    align-items: flex-end;
    @include bp($xs){
      align-items: center;
    }
}

.footer__textWrapper{
    display: flex;
    flex-direction: column;
    margin-left: 16px;
}

.footer__title{
    font-family: $font;
    font-style: normal;
    font-weight: 700;
    font-size: rem(18px);
    line-height: rem(24px);
    color: $color-black;

    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    @include bp($xs){
      padding: 16px 0;
    }
}

.footer__outerLink{
    position: absolute;
    bottom: -16px;
    left: 52px;
    display: flex;
    align-items: center;
    min-width: 12ch;

      &:hover .footer__socialIcon {
        filter: invert(100%);
      }

      &:hover .footer__linkText::before {
        transform-origin: left;
        transform: scaleX(1);
      }
}

.footer__linkText{
    position: relative;
    line-height: rem(32px);

    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        border-radius: 4px;
        background-color: #18272F;
        bottom: 0;
        left: 0;
        transform-origin: right;
        transform: scaleX(0);
        transition: transform .3s ease-in-out;
      }
}

.footer__innerNavTitle{
    font-family: $font;
    font-style: normal;
    font-weight: 700;
    font-size: rem(18px);
    line-height: rem(24px);
    color: $color-black;
    padding-top: 16px;
    padding-bottom: 8px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    // font-family: $font;
    // font-style: normal;
    // font-weight: 700;
    // font-size: rem(20px);
    // line-height: rem(29px);
    margin-top: 8px;
    margin-bottom: 0px;
}

.footer__list{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 56px;
}

.footer__item{
    padding: 4px 0;
}
.footer__link{
    padding: 4px 0;
    // padding-right: 24px;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        border-radius: 4px;
        background-color: #18272F;
        bottom: 0;
        left: 0;
        transform-origin: right;
        transform: scaleX(0);
        transition: transform .3s ease-in-out;
      }
      
      &:hover::before {
        transform-origin: left;
        transform: scaleX(1);
      }
      
}

.footer__paragraph{
  display: none;
    @include bp($xs){
      display: block;
      max-width: 50ch;
    }
}

.footer__socialIcon{
    width: 48px;
    padding-right: 12px;
    transition: 0.3s filter linear, 0.3s -webkit-filter linear;
}

.footer__quote{
    font-family: $font;
    font-style: normal;
    font-weight: 700;
    font-size: rem(18px);
    line-height: rem(24px);
    text-align: center;
    @include bp($xs){
      font-size: rem(22px);
    }
}

.footer__quoteContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 80px;
    padding-top: 64px;
}

.footer__arrow{
    width: 40px;

    &:nth-of-type(1){
        transform: rotate(180deg);
    }
    @include bp($xs){
      margin: 0 16px;
    }
}

.footer__bottomParagraph{
    position: relative;
    text-align: center;
    padding-right: 16px;
    font-size: rem(16px);
    color: $color-black;
    margin: 10px 0;
    // -webkit-box-shadow: inset 0px 0px 5px 20px $color-asphalt-gray; 
    // box-shadow: inset 0px 0px 5px 20px $color-asphalt-gray;


    @include bp($xs){
      position: absolute;
      padding: 8px;
      bottom: 0;
      right: 0;
      text-align: right;
      margin: 0;
    }
}

.footer__wrapper{
  @include bp($xs){
    display: flex;
  }

}

.footer__left{
  flex-basis: 0;
  flex-grow: 7;
}
.footer__right{
  flex-basis: 0;
  flex-grow: 3;

  margin: 24px 0 16px;
  @include bp($xs){
    margin: 0;
  }
}