.app{
    max-width: 1440px;
    margin: auto;
}

.sr-only {
    position:absolute;
    left:-10000px;
    top:auto;
    width:1px;
    height:1px;
    overflow:hidden;
}


// Splide
.splide{
    padding: 1em 0 3em;
    @include bp($xs){
        padding: 1.5em 3em 3em;
    }
}

$captionHeight: 30px;

.splide__arrow{
    display: none;
    @include bp($xs){
        display: block;
        transform: translateY(calc(-50% - #{$captionHeight}));
    }
}